import React from "react";
import Context from "../../Context";
import key from "../../util/key";
import { withRouter } from "react-router-dom";
import { injectIntl } from "react-intl";
import { Helmet } from "react-helmet";
import visualState from "../../util/visualState";
import { Button, message, Spin, Tabs } from "antd";
import StationsTable from "./StationsTable";
import StationsTiles from "./StationsTiles";
import {
  AppstoreOutlined,
  CopyOutlined,
  InsertRowAboveOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import StationsMap from "./StationsMap";
import EditModal from "../../util/EditModal";
import stationEditForm from "./stationEditForm";
import Flex from "../../util/Flex";
import { connect } from "react-redux";

const vState = new visualState("Stations");

class Stations extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);

    this.state = {
      pending: false,
      stations: [],
      stationId: null,
      editModalVisible: false,
      stationsKey: key(),
      mode: vState.read("mode", "tiles"),
    };
  }
  m = (key, prefix = "stations.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  modeSwitch = (mode) => {
    this.setState({ mode }, () => vState.save({ mode }));
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };
  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  onMethodFinish = () => {
    this.load();
  };

  load = async () => {
    this.pendingStart();
    let stations = await this.context.api.q(
      "/charge_point/list",
      {},
      { toArray: true }
    );
    this.setState({ stations }, () => this.pendingEnd());
  };

  editModalToggle = () => {
    this.setState((state) => {
      return { editModalVisible: !state.editModalVisible };
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.load();
  };

  render() {
    const { pending, stations, stationsKey, mode } = this.state;
    const { user } = this.props;

    let canAddStations = user?.access?.charge_point?.c ? true : false;

    return (
      <div>
        <Helmet>
          <title>{this.m("stations")}</title>
        </Helmet>

        <Flex between className="gx-mb-4 flex-reset-lg">
          <h2 className="title gx-mb-2">{this.m("stations")}</h2>
          {canAddStations && (
            <Flex className="flex-reset-lg">
              <div className="gx-text-right gx-mr-4">
                <div>{this.m("connection")}:</div>
                <div className="gx-text-muted gx-font-weight-light gx-mt-1 break-all">
                  {user.connectionLink}
                  <span
                    className="gx-ml-2 gx-link"
                    onClick={() => {
                      navigator.clipboard.writeText(user.connectionLink);
                      message.success(this.m("copied"));
                    }}
                  >
                    <CopyOutlined />
                  </span>
                </div>
              </div>
              <Button
                className="gx-mb-0"
                type="primary"
                onClick={() => this.editModalToggle()}
                icon={<PlusOutlined />}
              >
                {this.m("add")}
              </Button>
            </Flex>
          )}
        </Flex>
        <EditModal
          essence="charge_point"
          onClose={this.editModalToggle}
          form={stationEditForm.call(this)}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
        />

        <Tabs
          defaultActiveKey={mode}
          onChange={this.modeSwitch}
          className="gx-mb-4"
        >
          <Tabs.TabPane
            tab={<AppstoreOutlined title={this.m("as.tiles")} />}
            key="tiles"
          >
            <Spin spinning={pending}>
              <StationsTiles
                key={stationsKey}
                stations={stations}
                onMethodFinish={this.onMethodFinish}
              />
            </Spin>
          </Tabs.TabPane>
          <Tabs.TabPane
            tab={
              <div
                className="icon icon-map-simple"
                title={this.m("as.map")}
              ></div>
            }
            key="map"
          >
            <Spin spinning={pending}>
              <StationsMap
                key={stationsKey}
                stations={stations}
                onMethodFinish={this.onMethodFinish}
                showMoreButton
                className="gx-st-map big"
              />
            </Spin>
          </Tabs.TabPane>
          <Tabs.TabPane
            tab={<InsertRowAboveOutlined title={this.m("as.table")} />}
            key="table"
          >
            <StationsTable onMethodFinish={this.onMethodFinish} />
          </Tabs.TabPane>
        </Tabs>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.user,
});

export default connect(mapStateToProps, null)(withRouter(injectIntl(Stations)));
